import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';

@Component({
  selector: 'app-admin-material',
  templateUrl: './admin-material.component.html',
  styleUrls: ['./admin-material.component.css']
})
export class AdminMaterialComponent implements OnInit {
  materials;

  getMaterials(){
    this.http.get("http://localhost:8080/CompSystem/admin/allMaterial")
      .map(response => console.log("AdminMaterialComponent.getMaterials:", response.json().materials.list) || response.json().materials.list)
      .subscribe(response => this.materials = response);
  }

  pass(material,pass){
    var url = "http://localhost:8080/CompSystem/admin/reviewMaterial" + "?mid=" +material.materialId;
    if(pass===0){
      url = url + "&pass=0";
    }
    this.http.get(url)
    .map(response => console.log("AdminMaterialComponent.pass:", response.json()) || response.json())
    .subscribe(response => {
      console.log(response);
      this.getMaterials();
      alert("审核成功!");
    });
  }

  constructor(private http: Http) { }

  ngOnInit() {
    this.getMaterials();
  }

}



