import { Component, OnInit } from '@angular/core';

import { LoginService } from '../_services';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css'],
  providers: [LoginService]
})
export class UserComponent implements OnInit {
  private user: any;
  private role: any;
  links: Object[];

  constructor(private loginService: LoginService) { }

  ngOnInit() {
    this.user = this.loginService.getUser();
    this.role = this.loginService.getRole();
    if (this.user)
      switch (this.role) {
        case "admin": {
          this.links = [
            // { href: '/admin', name: '仪表板' },
            { href: 'article', name: '管理文章' },
            { href: 'contest', name: '管理竞赛' },
            { href: 'works', name: '管理作品'},
            { href: 'files', name: '管理文件'},
            { href: 'material', name: '管理材料'}
          ];
        }
          break;
        case "teacher": {
          this.links = [
            { href: '/teacher', name: '个人信息', icon:'person' },
            { href: 'declaration', name: '申报竞赛', icon:'create' },
            { href: 'save', name: '申报书草稿箱(暂)'},
            { href: 'declarationList', name: '申报竞赛列表', icon:'view_list' },
            { href: 'feedbackAward', name: '反馈获奖', icon:'feedback'}
          ];
          if (this.user.examiner === 1) {
            this.links.splice(1,2);
            this.links.push({ href: 'examiner', name: '审批竞赛', icon:'rate_review' });
          }
        }
          break;
        case "student": {
          this.links = [
            { href: '/student', name: '个人信息', icon:'person' },
            { href: 'contest', name: '参加竞赛', icon:'star' },
            { href: 'group', name: '我的竞赛小组'},
            { href: 'award', name: '获奖情况', icon:'info' }
          ];
        }
          break;
        default: ;
      }
    if (this.role === "teacher" || this.role === "student") {
      this.links.push({ href: '/notification', name: '消息', icon:'message' });
    }
  }

}



