import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';

import { LoginService} from '../../_services';

@Component({
  selector: 'app-declaration-save',
  templateUrl: './declaration-save.component.html',
  styleUrls: ['./declaration-save.component.css'],
  providers: [LoginService]
})
export class DeclarationSaveComponent implements OnInit {
  teacher;

  getSaved(){
    this.http.get("http://localhost:8080/CompSystem/teacher/getNoSubmitComp" +"?teacherNo="+ this.teacher.teacherno)
      .map(response => console.log("DeclarationSaveComponent.getSaved:",response.json()) || response.json())
      .subscribe(response => {});
  }
  constructor(private http: Http,private loginService: LoginService) { }

  ngOnInit() {
    this.teacher = this.loginService.getUser();
    this.getSaved();
  }

}



