import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from './guard';
import { HomeComponent } from './home';
import {
  AdminHomeComponent, TeacherInfoComponent, TeacherContestComponent, StudentInfoComponent,
  StudentContestComponent, UserComponent, AdminWorksComponent, AdminFilesComponent,
  AdminContestComponent, AdminMaterialComponent, StudentGroupComponent
} from './user';
import { ArticleComponent, ArticleListComponent, ArticleEditComponent,
  ArticleDetailComponent,ArticleSearchComponent } from './article';
import { ContestComponent, ContestDetailComponent, ContestListComponent,
  ContestDoneComponent } from './contest';
import { DeclarationComponent, DeclarationDetailComponent, DeclarationSaveComponent } from './declaration';
import { AwardListComponent } from './award';
import { ExaminerComponent } from './declaration/examiner';
import { DeclarationListComponent } from './declaration/declaration-list';
import { NotificationComponent } from './notification';

const appRoutes: Routes = [
  {
    path: '',
    component: HomeComponent
  },{
    path: 'searchArticle/:url',
    component: ArticleSearchComponent
  }, {
    path: 'admin',
    component: UserComponent,
    children: [{
      path: '',
      component: AdminContestComponent
    }, {
        path: 'article',
        component: ArticleComponent,
        children: [{
          path: '',
          component: ArticleListComponent
        }, {
            path: 'edit/:id',
            component: ArticleEditComponent
          }, {
            path: 'publish/:comid',
            component: ArticleEditComponent
          }, {
            path: 'push/:awardid',
            component: ArticleEditComponent
          }, {
            path: 'edit',
            component: ArticleEditComponent
          }, {
            path: 'detail/:id',
            component: ArticleDetailComponent
          }]
      }, {
        path: 'contest',
        component: AdminContestComponent,
        children: [{
          path: 'publish/:comid',
          component: ArticleEditComponent
        }]
      }, {
        path: 'works',
        component: AdminWorksComponent
      }, {
        path: 'files',
        component: AdminFilesComponent
      }, {
        path: 'material',
        component: AdminMaterialComponent
      }]
  }, {
    path: 'teacher',
    component: UserComponent,
    children: [{
      path: '',
      component: TeacherInfoComponent
    }, {
        path: 'declaration',
        component: DeclarationComponent
      }, {
        path: 'save',
        component: DeclarationSaveComponent
      }, {
        path: 'declarationList',
        component: TeacherContestComponent
      }, {
        path: 'declaration/detail/:id',
        component: DeclarationDetailComponent
      }, {
        path: 'examiner',
        component: ExaminerComponent
      }, {
        path: 'feedbackAward',
        component: ContestDoneComponent
      }, {
        path: 'notification',
        component: NotificationComponent
      }]
  }, {
    path: 'student',
    component: UserComponent,
    children: [{
      path: '',
      component: StudentInfoComponent
    }, {
        path: 'contest',
        component: StudentContestComponent
      }, {
        path: 'group',
        component: StudentGroupComponent
      }, {
        path: 'award',
        component: AwardListComponent
      }, {
        path: 'notification',
        component: NotificationComponent
      }]
  }, {
    path: 'article',
    component: ArticleComponent,
    children: [{
      path: '',
      component: ArticleListComponent
    }, {
        path: 'detail/:id',
        component: ArticleDetailComponent
      }]
  }, {
    path: 'contest',
    component: ContestComponent,
    children: [{
      path: '',
      component: ContestListComponent
    }, {
        path: 'detail/:id',
        component: ContestDetailComponent
      }]
  },

  // otherwise redirect to home
  // { path: '**', redirectTo: '' }
];

export const routing = RouterModule.forRoot(appRoutes);



