import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';

import { LoginService } from '../../../_services';

@Component({
  selector: 'app-student-group',
  templateUrl: './student-group.component.html',
  styleUrls: ['./student-group.component.css']
})
export class StudentGroupComponent implements OnInit {
  student;
  groups;
  cancel(group){
    var v = group.groupList.find(e => this.student.studentno == e.studentno);
    this.http.get("http://localhost:8080/CompSystem/student/outGroup" + "?id="+v.id)
      .subscribe(() => {
        this.getGroups();
        alert("取消成功!");
      })
  }
  pass(group,pass){
    this.http.get("http://localhost:8080/CompSystem/student/setCaptainStatus" + "?captainstatus="+pass+"&id="+group.id)
      .subscribe(() => {
        //重新读取数据
        this.getGroups();
        alert("审核成功!");
      });
  }
  getGroups(){
    this.http.get("http://localhost:8080/CompSystem/student/getCompGroupMembers"+"?studentNo=" + this.student.studentno)
      .map(response => console.log("StudentGroupComponent.getGroups:",response.json()) || response.json())
      .subscribe(response => {
        this.groups = response;
        this.groups.forEach((currentValue,index,array) => {
          currentValue.memberCount = 0;
          currentValue.groupList.forEach((v,i,a) => {
            if(v.captainstatus == 1){
              ++currentValue.memberCount;
            }
            v.student = currentValue.members.find(e => v.studentno == e.studentno);
          });
          console.log(currentValue.groupList);
          currentValue.captainGroup = currentValue.groupList.find((e,i,a) => e.iscaptain);
          console.log("currentValue.captainGroup",currentValue.captainGroup);
          currentValue.captainStudent = currentValue.members.find(member => currentValue.captainGroup.studentno === member.studentno);
          console.log("currentValue.captainStudent",currentValue.captainStudent);

        });
      });
  }

  constructor(private http:Http,private loginService: LoginService) { }

  ngOnInit() {
    this.student = this.loginService.getUser();
    this.getGroups();
  }

}



