import { Injectable } from '@angular/core';
import { Http, Headers, Response, RequestOptions, URLSearchParams } from '@angular/http';
import { Title }     from '@angular/platform-browser';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { Login, Role, Admin, Teacher, Student } from '../_models';

import { ROLES, ADMINS, TEACHERS, STUDENTS } from '../_datas';

@Injectable()
export class LoginService {
  private loginUrl: string = "http://localhost:8080/CompSystem/login";
  private rolesUrl: string = "http://localhost:8080/CompSystem/getRoles";
  private user: any;
  private role: any;
  private componentMethodCallSource = new Subject<any>();
  componentMethodCalled$ = this.componentMethodCallSource.asObservable();

  public openDialog() {
    console.log("next!!");
    this.componentMethodCallSource.next();
  }

  autoAuth(){
    console.log(this.role,this.user);

  }

  constructor(private http: Http, private titleService: Title) {
    if (localStorage.getItem('currentUser') === undefined) {
      this.logout();
    }
    this.user = JSON.parse(localStorage.getItem('currentUser'));
    this.role = JSON.parse(localStorage.getItem('currentRole'));
    if (this.role && this.user) {
      var form = {
        account: "",
        password: this.user.password,
        role: this.role
      }
      switch (this.role) {
        case "admin": {
          form.account = this.user.adminno;
        }
          break;
        case "teacher": {
          form.account = this.user.teacherno;
        }
          break;
        case "student": {
          form.account = this.user.studentno;
        }
          break;
        default: this.logout();
      }
      this.login(form).subscribe(() => {});
    }
  }

  login(form: Login): Observable<Object> {
    let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
    let options = new RequestOptions({ headers: headers });
    let urlSearchParams = new URLSearchParams();
    urlSearchParams.append('account', form.account);
    urlSearchParams.append('password', form.password);
    urlSearchParams.append('role', form.role);
    return this.http.post(this.loginUrl, urlSearchParams, options)
      .map(response => {
        console.log("LoginService.login:",response.json());
        if (response.json().user === undefined || response.json().user === null) {
          this.logout();
          alert("账号或者密码错误!");
        }
        else {
          this.user = response.json().user;
          localStorage.setItem('currentUser', JSON.stringify(this.user));
          this.role = form.role;
          localStorage.setItem('currentRole', JSON.stringify(this.role));
          var title;
          switch(this.role){
            case "admin":{
              title = "管理员:"+this.user.adminname;
            }break;
            case "teacher":{
              title = "老师("+this.user.department+"):"+this.user.teachername;
              if(this.user.examiner == 1){
                title +="(审批员)";
              }
            }break;
            case "student":{
              title ="学生:"+this.user.studentname;
            }break;
            default: title = "游客";
          }
          this.titleService.setTitle(title);
          return console.log("login:", this.user) || this.user;
        }
      })
      .catch(this.handleError);
  }

  logout(): void {
    // remove user from local storage to log user out
    localStorage.removeItem('currentUser');
    localStorage.removeItem('currentRole');
    this.user = null;
  }

  getUser() {
    return console.log("loginService.getUser:", this.user) || this.user;
  }

  getRole() {
    return console.log("loginService.getRole:", this.role) || this.role;
  }

  getRoles(): Promise<Role[]> {
    return Promise.resolve(ROLES);
    // this.http.get(this.rolesUrl)
    //   .toPromise()
    //   .then(response => response.json() as Role[])
    //   .catch(this.handleError);
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Promise.reject(errMsg);
  }
}



