import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';

import { Teacher } from '../../../_models';
import { LoginService, TeacherService } from '../../../_services';

@Component({
  selector: 'app-teacher-info',
  templateUrl: './teacher-info.component.html',
  styleUrls: ['./teacher-info.component.css'],
  providers: [TeacherService]
})
export class TeacherInfoComponent implements OnInit {
  teacher: Teacher;
  teacherEditCopy: Teacher;
  examiner;
  teacherInfo = {
    tno: '职工号',
    name: '姓名',
    gender: '性别',
    birth: '出生日期',
    department: '部门',
    subject: '科室',
    phone: '联系电话',
    title: '职称',
    post: '职务',
    email: '电子邮箱',
    type: '教职工类别',
    direction: '教学研究方向',
    education: '学历',
    lab: '是否实验室人员',
    password: '密码',
    examiner: '是否审批人员'
  }
  disableEdit: boolean = true;

  constructor(private loginService: LoginService,
    private teacherService: TeacherService) { }

  setExaminer(){
    if(this.examiner === "是"){
      this.teacher.examiner = 1;
    }else if(this.examiner === "否"){
      this.teacher.examiner = 0;
    }else {
      alert(this.teacherInfo.examiner+":输入错误!");
    }
  }

  ngOnInit() {
    this.teacher = this.loginService.getUser();
    if(this.teacher.examiner === 0){
      this.examiner = "否";
    }else if(this.teacher.examiner === 1) {
      this.examiner = "是";
    }
  }

  toggleEdit() {
    this.disableEdit = !this.disableEdit;
  }

  edit() {
    this.teacherEditCopy = JSON.parse(JSON.stringify(this.teacher));
  }

  save() {
    this.teacherService.update(this.teacher)
      .subscribe(response => {
        if (!response)
          this.teacher = this.teacherEditCopy;
      });
  }

  cancel() {
    this.teacher = this.teacherEditCopy;
  }
}



