import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { Router } from '@angular/router';

import { ArticleService } from './_services';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [ArticleService]
})

export class AppComponent implements OnInit {
  title = "学科竞赛系统";
  searchMode = false;
  articleTypes;

  searchToggle() {
    this.searchMode = !this.searchMode;
  }


  constructor(private http: Http,private articleService: ArticleService,
  private router: Router) { }

  ngOnInit(): void {
    this.getArticleTypes();
  }

  getSearchArticles(search) {
    var url = "?searchInput=" + search.word + "&type=" + search.type;
    this.router.navigate(['/searchArticle', url]);
  }

  getArticleTypes() {
    this.articleService.getArticleTypes().then(
      articleTypes => this.articleTypes = articleTypes
    );
  }
}



