import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Http } from '@angular/http';

import { ContestService,AwardService } from '../../../_services';

@Component({
  selector: 'app-admin-contest',
  templateUrl: './admin-contest.component.html',
  styleUrls: ['./admin-contest.component.css'],
  providers: [AwardService,ContestService]
})
export class AdminContestComponent implements OnInit {
  unContests;
  unAwards;

  publish(item) {
    this.http.get("http://localhost:8080/CompSystem/admin/initArticle" + "?comId=" + item.id)
      .map(response => console.log("AdminContestComponent.publish:", response.json()) || response.json())
      .subscribe(response => {
        console.log("AdminContestComponent.publish.get",response)
        this.router.navigate(['article/edit/' , response]);
      });
  }

  detailToggle(v){
    v.detailToggle = !v.detailToggle;
  }

  getUnPub() {
    this.contestService.getUnPubComps()
      .subscribe(response => {
        this.unContests = response;
        this.unContests.forEach((e,i,a) => {
          e.detailToggle = false;
          this.http.get("http://localhost:8080/CompSystem/teacher/getTeacherInfo"+"?teacherNo="+e.leaderNo)
            .map(response => console.log("teacher/getTeacherInfo",response.json()) || response.json())
            .subscribe(response => e.teacher = response);
        });
      });
    this.awardService.getUnPubAwards()
      .subscribe(response => {
        this.unAwards = response;
        this.unAwards.forEach((e,i,a) => {
          e.detailToggle = false;
        });
      });
  }

  constructor(private router: Router,private http: Http,
    private contestService: ContestService,private awardService: AwardService) { }

  ngOnInit() {
    this.getUnPub();
  }

}



