import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';

import { AwardService } from '../_services';

@Component({
  selector: 'app-works',
  templateUrl: './works.component.html',
  styleUrls: ['./works.component.css'],
  providers: [AwardService]
})
export class WorksComponent implements OnInit {
  works;

  constructor(private awardService: AwardService,private http: Http) { }

  ngOnInit() {
    this.getWorks();
  }

  getWorks() {
    this.http.get("http://localhost:8080/CompSystem/index/productionList" + "?pageSize=4")
      .map(response => console.log("WorksComponent.getWorks:",response.json().produPageInfo.list) || response.json().produPageInfo.list)
      .subscribe(works => this.works = works);
  }
}



