import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'materialPass'
})
export class MaterialPassPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    if(value){
      return value.filter(v => v.status === 0);
    }
  }

}



