import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Http } from '@angular/http';

import { ArticleService } from '../../_services/article.service';
import { Article } from '../../_models';
import { ARTICLETYPES } from '../../_datas';

@Component({
  selector: 'app-article-list',
  templateUrl: './article-list.component.html',
  styleUrls: ['./article-list.component.css'],
  providers: [ArticleService]
})
export class ArticleListComponent implements OnInit {
  articles: Article[] = [];
  ARTICLETYPES;
  articleTypeOnes;
  articleTypeTwos;
  articleTypeThrees;

  delete(article) {
    this.http.get("http://localhost:8080/CompSystem" + "/admin/deleteArticle" + "?articleId=" + article.articleId)
      .map(response => console.log("ArticleListComponent.delete:", response.json()) || response.json())
      .subscribe(() => {
        this.getArticles();
        this.getArticlesByType();
        alert("删除成功!");
      });
  }

  getArticlesByType() {
    this.http.get("http://localhost:8080/CompSystem/admin/article" + "?type=1" + "&pageSize=5")
      .map(response => {
        console.log("HomeComponent.getArticlesByType:", response.json());
        if (response.json()) {
          console.log(response.json());
          if (response.json().articlePageInfo) {
            console.log(response.json().articlePageInfo)
            return response.json().articlePageInfo.list;
          }
        } else return [];
      })
      .subscribe(response => this.articleTypeOnes = response);
    this.http.get("http://localhost:8080/CompSystem/admin/article" + "?type=2" + "&pageSize=5")
      .map(response => {
        console.log("HomeComponent.getArticlesByType:", response.json());
        if (response.json()) {
          console.log(response.json());
          if (response.json().articlePageInfo) {
            console.log(response.json().articlePageInfo)
            return response.json().articlePageInfo.list;
          }
        } else return [];
      })
      .subscribe(response => this.articleTypeTwos = response);
    this.http.get("http://localhost:8080/CompSystem/admin/article" + "?type=3" + "&pageSize=5")
      .map(response => {
        console.log("HomeComponent.getArticlesByType:", response.json());
        if (response.json()) {
          console.log(response.json());
          if (response.json().articlePageInfo) {
            console.log(response.json().articlePageInfo)
            return response.json().articlePageInfo.list;
          }
        } else return [];
      })
      .subscribe(response => this.articleTypeThrees = response);
  }

  constructor(private articleService: ArticleService,
    private router: Router, private http: Http) {
    Object.assign(this, { ARTICLETYPES });
  }

  ngOnInit() {
    this.getArticles();
    this.getArticlesByType();
  }

  getArticles() {
    this.articleService.getArticles()
      .subscribe(articles => this.articles = articles);
  }
}



