import { Component, OnInit } from '@angular/core';

import { AwardService } from '../_services';

import { single } from '../_datas';

@Component({
  selector: 'app-chart',
  templateUrl: './chart.component.html',
  styleUrls: ['./chart.component.css'],
  providers: [AwardService]
})
export class ChartComponent implements OnInit {
  single: any[];
  chartType: string;
  //option
  showXAxis = true;
  showYAxis = true;
  gradient = false;
  showLegend = true;
  showXAxisLabel = true;
  xAxisLabel = '系别';
  showYAxisLabel = true;
  yAxisLabel = '奖项数量';
  analysisAwars;
  analysisComps;

  colorScheme: any = {
    domain: ['#F44336', '#FF9800', '#FFEB3B', '#4CAF50', '#2196F3', '#3F51B5', '#9C27B0']
  };

  select(data) {
    console.log('Item clicked', data);
  }

  constructor(private awardService: AwardService) {
    Object.assign(this, {single});
  }

  ngOnInit() {
    this.chartType = "bar-vertical";
    this.getAnaly();
  }
  getAnaly() {
    this.awardService.getAnalyAward()
      .subscribe(response => this.analysisAwars = response.analysisAwars
        .map((v,i,a) => {return {name:v[0].department,value:v[0].awardsCount}}));
    this.awardService.getAnalyComp()
      .subscribe(response => this.analysisComps = response
        .map((v,i,a) => {return {name:v[0].department,value:v[0].compCount}}));
  }

  CHARTTYPES = [{name:'垂直条形图', value:'bar-vertical'}, {name:'进阶饼状图', value:'advanced-pie-chart'}];
}



