import { Component, OnInit, ViewChild,Input } from '@angular/core';
import { NgForm, FormControl } from '@angular/forms';
import { MdDialog, MdDialogRef } from '@angular/material/dialog';
import { Http } from '@angular/http';
import 'rxjs/add/operator/startWith';

import { ContestService, LoginService } from '../_services';
import { Contest, Schedule, Budget, Hour, Teacher } from '../_models';
import { TEACHERS, DEPARTMENTS } from '../_datas';

@Component({
  selector: 'app-declaration',
  templateUrl: './declaration.component.html',
  styleUrls: ['./declaration.component.css'],
  providers: [ContestService, LoginService]
})
export class DeclarationComponent implements OnInit {
  @Input('passComid') passComid;
  teacher;
  teachers;
  conditions;
  DEPARTMENTS;
  @ViewChild('baseInfo') baseInfo;
  @ViewChild('situation') situation;
  @ViewChild('plan') plan;
  progress = 20;
  bufferValue = 110;
  requiredProgress = 0;
  allCount = 0;
  requiredCount = 0;
  initProgress(baseInfo, situation, plan) {
    var allCount = 0;
    var requiredCount = 0;
    var setCount = 0;
    console.log(baseInfo);
    Object.keys(baseInfo.controls).map((e) => {
      ++allCount;
      if (baseInfo.controls[e]._errors) {
        if (baseInfo.controls[e]._errors.required) {
          ++requiredCount;
        }
      }
    });
    Object.keys(situation.controls).map((e) => {
      ++allCount;
      if (situation.controls[e]._errors) {
        if (situation.controls[e]._errors.required) {
          ++requiredCount;
        }
      }
    });
    Object.keys(plan.controls).map((e) => {
      ++allCount;
      if (plan.controls[e]._errors) {
        if (plan.controls[e]._errors.required) {
          ++requiredCount;
        }
      }
    });
    console.log("DeclarationComponent.initProgress:", "allcount:", allCount, "requiredCount:", requiredCount);
    this.allCount = allCount;
    this.requiredCount = requiredCount;
  }
  setProgress(baseInfo, situation, plan) {
    var count = 0;
    if (baseInfo.name !== "") {
      ++count;
    }
    if (baseInfo.leader !== "") {
      ++count;
    }
    if (baseInfo.unit !== "") {
      ++count;
    }
    if (situation.host !== "") {
      ++count;
    }
    if (situation.time !== "") {
      ++count;
    }
    if (situation.isNeedGroup === 1) {
      if (situation.groupNum !== null || situation.groupNum !== "") {
        ++count;
      }
    } else if (situation.isNeedGroup === 0) {
      ++count;
    }
    if (situation.place !== "") {
      ++count;
    }
    if (situation.object !== "") {
      ++count;
    }
    if (situation.intro !== "") {
      ++count;
    }
    if (plan.plan !== "") {
      ++count;
    }
    if (plan.tgoal !== "") {
      ++count;
    }
    if (plan.wgoal !== "") {
      ++count;
    }
    this.progress = 10 * count;
    console.log(count + "!!!");
  }
  getConditions() {
    this.http.get("http://localhost:8080/CompSystem/teacher/getConditionsByType"+ "?type=0")
      .map(response => console.log("DeclarationComponent.getConditions?type=0:", response.json()) || response.json())
      .subscribe(response => this.conditions = response);
        this.http.get("http://localhost:8080/CompSystem/teacher/getConditionsByType"+ "?type=1")
          .map(response => console.log("DeclarationComponent.getConditions?type=1:", response.json()) || response.json())
          .subscribe(response => this.DEPARTMENTS = response);
  }
  getTeachers() {
    return this.http.get("http://localhost:8080/CompSystem/teacher/getAllTeacher")
      .map(response => console.log("DeclarationComponent.getTeachers:", response.json()) || response.json());
  }
  // TEACHERS: Teacher[];
  table = {
    schedule: [
      { prop: 'tno' },
      { name: 'content' },
      { name: 'place' },
      { name: 'date' },
      { name: 'hour' }
    ],
    budget: [
      { prop: 'subject' },
      { name: 'sum' },
      { name: 'reasons' }
    ],
    hour: [
      { prop: 'type' },
      { name: 'hour' },
      { name: 'remarks' }
    ]
  }
  schedules: Schedule[];
  budgets: Budget[];
  hours: Hour[];
  scheduleVisible = true;
  budgetVisible = true;
  hourVisible = true;
  guideTeachers: GuideTeacher[];
  gTeachers;
  title = '申报书';
  declaration = {
    baseInfo: {
      title: '基本信息',
      name: '竞赛名称',
      leader: '竞赛负责人',
      unit: '所在单位(盖章)',
      type: '竞赛类别',
      phone: '联系电话',
      email: '电子邮箱',
      date: '填表日期'
    },
    situation: {
      title: '竞赛情况',
      host: '院级以上竞赛举办者',
      time: '竞赛时间',
      place: '竞赛地点',
      object: '参赛对象',
      people: '参赛人数',
      sponsor: '校外资助单位',
      intro: '竞赛简介'
    },
    plan: {
      title: '实施方案',
      instructor: '指导教师',
      instructorNo: '教职工号',
      plan: '学科竞赛实施方案',
      schedule: {
        title: '培训工作安排',
        content: '培训内容',
        place: '培训地点',
        date: '培训日期',
        hour: '学时'
      }
    },
    goal: {
      title: '目标',
      tgoal: '能力训练目标',
      wgoal: '竞赛获奖目标'
    },
    budget: {
      title: '经费预算',
      scription: '经费预算(包括报名费、材料费、设备仪器费、交通差旅费等)',
      budget: {
        title: '经费预算',
        subject: '支出科目',
        sum: '金额(元)',
        reasons: '计算依据及理由'
      },
      hour: {
        title: '课时预算',
        type: '课时类别',
        hours: '课时数(学时)',
        remarks: '备注'
      }
    },
    opinion: {
      uopinion: '竞赛组织实施单位意见',
      tdopinion: '教学处意见'
    }
  };
  date: any;
  types = [
    { value: 'A类' },
    { value: 'B类' },
    { value: 'C类' }
  ];
  isNeedGroup = 1;

  teacherCtrl: FormControl;
  filteredTeachers: any;


  constructor(private contestService: ContestService, private http: Http, private loginService: LoginService) {
    Object.assign(this, { DEPARTMENTS });
    // this.DEPARTMENTS.unshift({ departmentId: 0, departmentName: "不限" });
    this.teacherCtrl = new FormControl();
    this.filteredTeachers = this.teacherCtrl.valueChanges
      .startWith(null)
      .map(name => this.filterTeachers(name));
  }

  filterTeachers(val) {
    return val ? this.teachers.filter(t => new RegExp(`^${val}`, 'gi').test(t.teachername))
      : this.teachers;
  }
  ngOnInit() {
    this.teacher = this.loginService.getUser();
    this.schedules = [];
    this.budgets = [];
    this.hours = [];
    this.guideTeachers = [];
    this.gTeachers = [];
    this.date = new Date().toLocaleDateString();
    this.getTeachers()
      .subscribe(response => this.teachers = response);
    this.getConditions();
  }

  // ngAfterViewChecked() {
  //   this.initProgress(this.baseInfo, this.situation, this.plan);
  // }

  addGuideTeacher(teacher) {
    this.gTeachers.push(teacher);
    this.guideTeachers.push(new GuideTeacher(teacher.teacherno));
    console.log("DeclarationComponent.guideTeachers:", this.guideTeachers);
    console.log("DeclarationComponent.gTeachers:", this.gTeachers);
    console.log("DeclarationComponent.teacherCtrl", this.teacherCtrl)
  }
  deleteGuideTeacher(teacher, index) {
    this.gTeachers.splice(index, 1);
    this.guideTeachers.splice(index, 1);
    console.log("DeclarationComponent.deleteGuideTeacher:", this.gTeachers, this.guideTeachers);
  }
  displayFn(teacher) {
    console.log("DeclarationComponent.displayFn(teacher):", teacher);
    if (teacher) {
      this.addGuideTeacher(teacher);
    }
    return teacher ? teacher.teachername : teacher;
  }

  addSchedule(scheduleF: NgForm) {
    this.schedules.push(Schedule.newInstance(scheduleF.value));
    scheduleF.resetForm();
  }
  deleteSchedule(schedule: Schedule) {
    var index = this.schedules.indexOf(schedule)
    if (index > -1) {
      this.schedules.splice(index, 1);
    }
    console.log(this.schedules);
  }

  addBudget(budget: NgForm) {
    this.budgets.push(Budget.newInstance(budget.value));
    budget.resetForm();
  }
  deleteBudget(budget: Budget) {
    var index = this.budgets.indexOf(budget)
    if (index > -1) {
      this.budgets.splice(index, 1);
    }
    console.log(this.budgets);
  }

  addHour(hour: NgForm) {
    this.hours.push(Hour.newInstance(hour.value));
    hour.resetForm();
  }
  deleteHour(hour: Hour) {
    var index = this.hours.indexOf(hour)
    if (index > -1) {
      this.hours.splice(index, 1);
    }
    console.log(this.hours);
  }

  toggleSchedule() {
    this.scheduleVisible = !this.scheduleVisible;
  }

  toggleBudget() {
    this.budgetVisible = !this.budgetVisible;
  }

  toggleHour() {
    this.hourVisible = !this.hourVisible;
  }
  comid;
  submit(baseInfo, situation, plan,saveStatus) {
    var competition: Contest = new Contest();
    competition.comname = baseInfo.name;
    competition.teacherno = this.teacher.teacherno;
    competition.unit = baseInfo.unit;
    competition.levelname = baseInfo.type;
    competition.department = this.teacher.department;
    competition.date = this.date;
    competition.host = situation.host;
    competition.time = situation.time;
    competition.place = situation.place;
    competition.introduce = situation.intro;
    competition.object = situation.object;
    competition.people = situation.people;
    competition.sponsor = situation.sponsor;
    competition.plan = plan.plan;
    competition.tgoal = plan.tgoal;
    competition.wgoal = plan.wgoal;
    competition.applybegindate = situation.applyBeginDate;
    competition.applyenddate = situation.applyEndDate;
    competition.isneedgroup = +this.isNeedGroup;
    competition.groupnum = situation.groupNum;
    competition.saveStatus = saveStatus;
    var declaration = {
      competition: competition,
      budgets: this.budgets,
      guideTeachers: [],
      hours: this.hours,
      schedules: this.schedules,
      compConditions: []
    }
    if (this.comid) {
      declaration.competition.comid = this.comid;
    }
    declaration.guideTeachers = this.guideTeachers;
    // this.schedules.forEach(schedule => declaration.guideTeachers.push(new GuideTeacher(schedule.teacherno)));
if(situation.condition!= "" && situation.condition !=null){
  declaration.compConditions.push({ conditionid: situation.condition });
}
    console.log("declaration:", declaration);

    this.contestService.create(declaration).subscribe(response => {
      this.comid = response.competition.comid;
      if(saveStatus===0){
        alert("已经成功保存到草稿箱");
      }else if(saveStatus===1){
        alert("已经成功提交");
      }
    });
  }
}

export class GuideTeacher {
  guideteaid: number;
  teacherno: number;
  comid: number;

  constructor(teacherno) {
    this.teacherno = teacherno;
  }
  static newInstance(guideTeacher) {
    return new GuideTeacher(guideTeacher.teacherno);
  }
}



