import { Component, OnInit, ViewChild } from '@angular/core';
import { Location }               from '@angular/common';
import { MdDialog, MdDialogRef } from '@angular/material/dialog';
import { ActivatedRoute, Params }   from '@angular/router';
import { Http } from '@angular/http';
import 'rxjs/add/operator/switchMap';

import { Article, Student } from '../../_models';
import { ArticleService, LoginService, ContestService } from '../../_services';

@Component({
  selector: 'app-article-detail',
  templateUrl: './article-detail.component.html',
  styleUrls: ['./article-detail.component.css'],
  providers: [ArticleService, LoginService, ContestService]
})
export class ArticleDetailComponent implements OnInit {
  private dialogRef: MdDialogRef<ApplyDialog>;
  article;
  groups: any = [];
  student: Student;
  role;
  groupVisible: boolean = false;
  contest;
  already;
  groupsno;
  currentUrl;

  shared(){

  }
  //报名
  apply() {
    this.openDialog();
  }

  cancel() {
    this.http.get("http://localhost:8080/CompSystem/student/outGroup" + "?id=" + this.groupsno)
      .subscribe(() => {
        this.already = false;
        alert("取消报名成功!");
      });
  }

  toggleGroup() {
    this.groupVisible = !this.groupVisible;
  }

  constructor(
    private articleService: ArticleService,
    private contestService: ContestService,
    private loginService: LoginService, public dialog: MdDialog,
    private route: ActivatedRoute,  private location: Location,
    private http: Http) { }
  countView(article){
    this.http.get("http://localhost:8080/CompSystem/index/article"+"?articleId=" + article.articleId)
      .map(response => console.log("ArticleDetailComponent.countView:", response.json()) || response.json())
      .subscribe(response => console.log("countView+1."));
  }
  ngOnInit() {
    this.route.params
      .switchMap((params: Params) => this.articleService.getArticle(+params['id']))
      .subscribe(article => {
        this.article = article;
        this.countView(article);
        this.contestService.getGroups(this.article.relationId)
          .subscribe(groups => {
            this.groups = groups;
            var group = groups.find(group => group.studentno === this.student.studentno);
            if(group){
              this.already = true;
              this.groupsno = group.groupsno;
            }
          });
          this.http.get("http://localhost:8080/CompSystem/teacher/getCompDetail" +"?comId="+this.article.relationId)
          .map(response => console.log("ArticleDetailComponent.OnInit:",response.json()) || response.json())
          .subscribe(response => this.contest = response.competition);
        // this.contestService.getContest(this.article.relationId)
          // .subscribe(contest => this.contest = contest);
      });
    this.student = this.loginService.getUser();
    this.role = this.loginService.getRole();
    this.currentUrl = "http://localhost:8080/CompSystem/#"+this.location.path();
    console.log("currentUrl",this.currentUrl);
  }

  openDialog() {
    if(this.role != 'student'){
      alert("请以学生的身份登录!");
    }else if(this.role == 'student'){
      this.dialogRef = this.dialog.open(ApplyDialog, {
        disableClose: false
      });

      this.dialogRef.componentInstance.student = this.student;
      this.contestService.getCompGuideTeacher(this.article.relationId)
        .subscribe(guideTeachers => this.dialogRef.componentInstance.guideTeachers = guideTeachers);

      this.dialogRef.componentInstance.contest = this.contest;
      this.dialogRef.componentInstance.groups = this.groups;
      this.dialogRef.componentInstance.student = this.student;

      this.dialogRef.afterClosed().subscribe(result => {
        if (result) {
          var f = {
            groupsname: result.groupsname,
            teacherno: result.teacherno,
            studentno: this.student.studentno,
            comid: this.contest.comid
          }
          this.contestService.createGroup(f).subscribe(() => console.log("create group sucess"));
        }
        this.dialogRef = null;
      });
    }
  }
}

@Component({
  selector: 'app-apply-dialog',
  templateUrl: './apply.dialog.html',
  styleUrls: ['./apply.dialog.css'],
  providers: [ContestService]
})
export class ApplyDialog implements OnInit {
  guideTeachers;
  contest;
  student;
  groupMode;
  groups;

  setGroupMode(groupMode) {
    this.groupMode = groupMode;
  }

  joinGroup(group) {
    this.http.get("http://localhost:8080/CompSystem" + "/student/joinGroup" + "?groupsNo=" + group.groupsno + "&studentNo=" + this.student.studentno)
      .subscribe(() => alert("申请成功!等待队长审批."));
  }

  constructor(public dialogRef: MdDialogRef<ApplyDialog>, private http: Http) { }

  ngOnInit() {
    console.log("groups:",this.groups);
  }
}



