import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { MdDialog, MdDialogRef } from '@angular/material/dialog';
import { LoginService } from '../../_services';

@Component({
  selector: 'app-examiner',
  templateUrl: './examiner.component.html',
  styleUrls: ['./examiner.component.css']
})
export class ExaminerComponent implements OnInit {
  private dialogRef: MdDialogRef<ExaminerDialog>;
  teacher;
  approComps;

  openDialog(item) {
    this.dialogRef = this.dialog.open(ExaminerDialog, {
      disableClose: false
    });

    this.dialogRef.componentInstance.comp = item;
    this.dialogRef.componentInstance.teacher = this.teacher;

    this.dialogRef.afterClosed().subscribe(result => {
      if (result) {
        var url;
        if (this.teacher.department !== "教学处") {
          url = "http://localhost:8080/CompSystem/teacher/updateCompStatusByDept";
        } else {
          url = "http://localhost:8080/CompSystem/teacher/updateCompStatusByTea";
        }
        this.http.post(url, result)
          .subscribe(() => alert("审批成功!"));
      }
      this.dialogRef = null;
    });
  }

  getApproCompList() {
    this.http.get("http://localhost:8080/CompSystem/teacher/getApproCompList" + "?teacherNo=" + this.teacher.teacherno)
      .map(response => console.log("TeacherContestComponent.getApproCompList:", response.json()) || response.json())
      .subscribe(response => this.approComps = response);
  }

  constructor(private http: Http, public dialog: MdDialog, private loginService: LoginService) { }

  ngOnInit() {
    this.teacher = this.loginService.getUser();
    this.getApproCompList();
  }

}

@Component({
  selector: 'app-examiner-dialog',
  templateUrl: './examiner.dialog.html',
  styleUrls: ['./examiner.dialog.css']
})
export class ExaminerDialog implements OnInit {
  comp;
  teacher;
  d = new Date();
  dStr = this.d.getFullYear() + "-" + (this.d.getMonth() + 1) + "-" + this.d.getDate();

  setComp(examiner) {
    console.log("ExaminerDialog.setComp(examiner):",examiner);
    if (this.teacher.department != "教学处") {
      this.comp.uopinion = examiner.opinion
      this.comp.dsign = this.teacher.teachername;
      this.comp.dsdate = this.dStr;
      this.comp.depspstatus = examiner.pass;
    } else {
      this.comp.tdopinion = examiner.opinion;
      this.comp.olsign = this.teacher.teachername;
      this.comp.dsdate = this.dStr;
      this.comp.teaspstatus = examiner.pass;
    }
    console.log("ExaminerDialog.setComp:",this.comp);
  }

  constructor(public dialogRef: MdDialogRef<ExaminerDialog>) { }

  ngOnInit() {

  }
}



