import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/toPromise';

import { Award } from '../_models';
import { AWARDS } from '../_datas';

@Injectable()
export class AwardService {
  private baseUrl: string = "http://localhost:8080/CompSystem"

  constructor(private http: Http) { }

  getAwards(studentNo: number) {
    return this.http.get(this.baseUrl + "/student/getAwards" + "?studentNo=" + studentNo)
      .map(response => console.log("AwardService.getAwards:", response.json()) || response.json())
      .catch(this.handleError);
  }

  getWorks() {
    return this.http.get(this.baseUrl + "/admin/productionList")
      .map(response => console.log("AwardService.getWorks:", response.json().produPageInfo.list) || response.json().produPageInfo.list)
      .catch(this.handleError);
  }

  addWorks(work) {
    let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
    let options = new RequestOptions({ headers: headers });
    let urlSearchParams = new URLSearchParams();
    urlSearchParams.append('proName', work.proName);
    urlSearchParams.append('award', work.award);
    urlSearchParams.append('membersName', work.membersName);
    urlSearchParams.append('introduction', work.introduction);
    return this.http.post(this.baseUrl + "/admin/uploadProduction", urlSearchParams, options)
      .map(response => console.log("AwardService.addWorks:", response.json()) || response.json())
      .catch(this.handleError);
  }

  getAnalyAward() {
    return this.http.get("http://localhost:8080/CompSystem/index/analysisAwars")
      .map(response => console.log("AdminContestComponent.getAnalyAward:", response.json()) || response.json())
  }
  getAnalyComp() {
    return this.http.get("http://localhost:8080/CompSystem/index/analysisComp")
      .map(response => console.log("AdminContestComponent.getAnalyComp:", response.json()) || response.json())
  }

  getUnPubAwards() {
    return this.http.get("http://localhost:8080/CompSystem/admin/awardList")
      .map(response => console.log("AdminContestComponent.getUnPub:", response.json().awardsList.list) || response.json().awardsList.list);
  }

  getUnPubAward(id: number) {
    return this.getUnPubAwards()
      .map(unPubAwards => {
        console.log(`id=${id}`, unPubAwards);
        var response = unPubAwards.find(unPubAward => unPubAward.competition.comid === id);
        console.log("ContestService.getUnPubAward:", response);
        return response;
      });
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Promise.reject(errMsg);
  }
}



