import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Http } from '@angular/http';

import { ArticleService, ContestService } from '../_services';

import { Article, ArticleType } from '../_models';
import { Contest } from '../_models';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  providers: [ArticleService, ContestService]
})
export class HomeComponent implements OnInit {

  articleTypes: ArticleType[] = [];
  articles: Article[] = [];
  contests: Contest[] = [];
  articleTypeOnes;
  articleTypeTwos;
  articleTypeThrees;

  getArticlesByType() {
    this.http.get("http://localhost:8080/CompSystem/index/articlesList" + "?type=1" +"&pageSize=5")
      .map(response => console.log("HomeComponent.getArticlesByType:", response.json().articlePageInfo.list) || response.json().articlePageInfo.list)
      .subscribe(response => this.articleTypeOnes = response);
    this.http.get("http://localhost:8080/CompSystem/index/articlesList" + "?type=2" +"&pageSize=5")
      .map(response => console.log("HomeComponent.getArticlesByType:", response.json().articlePageInfo.list) || response.json().articlePageInfo.list)
      .subscribe(response => this.articleTypeTwos = response);
    this.http.get("http://localhost:8080/CompSystem/index/articlesList" + "?type=3" +"&pageSize=5")
      .map(response => console.log("HomeComponent.getArticlesByType:", response.json().articlePageInfo.list) || response.json().articlePageInfo.list)
      .subscribe(response => this.articleTypeThrees = response);
  }

  apply(contest) {
    this.http.get("http://localhost:8080/CompSystem/index/getArticleByComp" + "?compId="+contest.comid)
      .map(response => console.log(`HomeComponent.apply:`,response.json()) || response.json())
      .subscribe(response => {
        this.router.navigate(['article/detail/' + response.articleId]);
      });
    // this.articleService.getArticleByComp(contest.comid)
    //   .subscribe(article => this.router.navigate(['article/detail/' + article.articleId]));
  }

  constructor(private router: Router, private articleService: ArticleService, private http: Http,
    private contestServer: ContestService) { }

  ngOnInit() {
    this.getArticleTypes();
    this.getArticles();
    this.getContests();
    this.getArticlesByType();
  }

  setColor(contest: Contest): string {
    let currentDate: number =
      (d => new Date(d))(new Date().toLocaleDateString()).valueOf();
    let applyBeginDate: number = new Date(contest.applybegindate).valueOf();
    let applyEndDate: number = new Date(contest.applyenddate).valueOf();
    let comDate: number = new Date(contest.time).valueOf();
    // 当前日期 在 报名开始之前
    if (currentDate < applyBeginDate) {
      return "#03A9F4";
    }
    // 当前日期 在 报名开始之后 && 报名结束之前
    else if (currentDate <= applyEndDate) {
      return "#4CAF50";
    }
    // 当前日期 在 报名结束之后 && 比赛结束之前
    else if (currentDate <= comDate) {
      return "#FF9800"
    }
    // 当前日期 在 比赛结束之后
    return "#9E9E9E";
  }

  getArticleTypes() {
    this.articleService.getArticleTypes().then(
      articleTypes => this.articleTypes = articleTypes
    );
  }

  getArticles(): void {
    this.articleService.getArticles().subscribe(articles => this.articles = articles);
  }

  getContests(): void {
    this.contestServer.getContests().subscribe(
      contests => this.contests = contests
    );
  }
}



