import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params }   from '@angular/router';
import 'rxjs/add/operator/switchMap';
import { Http } from '@angular/http';

import { ContestService,LoginService, TeacherService } from '../../_services';

@Component({
  selector: 'app-declaration-detail',
  templateUrl: './declaration-detail.component.html',
  styleUrls: ['./declaration-detail.component.css'],
  providers: [ ContestService, LoginService, TeacherService ]
})
export class DeclarationDetailComponent implements OnInit {
  teacher = {};
  declaration = {};
  leader = {};
  teachers = [];
  schedules = [];
  budgets = [];
  budgetsSum = 0;
  hours = [];
  hoursSum = 0;
  export(declaration){
    var url = "http://localhost:8080/CompSystem/teacher/getAprroveTable" + "?comId="+declaration.comid;
    window.location.href = url;
    //  this.http.get(url)
    //   .map(response => console.log("DeclarationDetailComponent.export:",response.json()) || response.json())
    //   .subscribe(response => console.log(response));
  }

  constructor(private route: ActivatedRoute, private contestService: ContestService,
    private loginService: LoginService,private teacherService: TeacherService, private http: Http) { }

  ngOnInit() {
    this.teacher = this.loginService.getUser();
    this.route.params
      .switchMap((params: Params) => this.contestService.getDeclaration(+params['id']))
      .subscribe(declaration => {
        this.declaration = declaration;
        this.teacherService.select(declaration.teacherno).subscribe(response => this.leader = response);
        this.http.get("http://localhost:8080/CompSystem/teacher/getCompDetail" + "?comId=" + declaration.comid)
          .map(response => console.log("DeclarationDetailComponent:",response.json()) || response.json())
          .subscribe(response => {
            this.teachers = response.teachers;
            this.schedules = response.schedules;
            this.budgets = response.budgets;
            this.budgets.forEach(budget => this.budgetsSum += budget.sum);
            this.hours = response.hours;
            this.hours.forEach(hour => this.hoursSum += hour.hours);
          });
      });
  }

}



