import { Component, OnInit, ElementRef, Input, ViewChild } from '@angular/core';
import { Http } from '@angular/http';
import { MdDialog, MdDialogRef } from '@angular/material/dialog';

import { LoginService } from '../../_services';

@Component({
  selector: 'app-contest-done',
  templateUrl: './contest-done.component.html',
  styleUrls: ['./contest-done.component.css'],
  providers: [LoginService]
})
export class ContestDoneComponent implements OnInit {
  private dialogRef: MdDialogRef<DoneDialog>;
  teacher;
  contests;

  getDoneContests(){
    this.http.get("http://localhost:8080/CompSystem/teacher/getEndCompetition" +"?teacherNo=" +this.teacher.teacherno)
      .map(response => console.log("ContestDoneComponent.getDoneContests:", response.json()) || response.json())
      .subscribe(response => this.contests = response);
  }
  constructor(public dialog: MdDialog,private http: Http, private loginService: LoginService) { }

  ngOnInit() {
    this.teacher = this.loginService.getUser();
    this.getDoneContests();
  }
    openDialog(contest) {
      this.dialogRef = this.dialog.open(DoneDialog, {
        disableClose: false
      });
      this.dialogRef.componentInstance.contest = contest;
      this.dialogRef.afterClosed().subscribe(() => {
        this.dialogRef = null;
      });
    }
}

@Component({
  selector: 'app-done-dialog',
  templateUrl: './done.dialog.html',
  styleUrls: ['./done.dialog.css'],
  providers: []
})
export class DoneDialog implements OnInit {
  @ViewChild('fileInput') inputEl: ElementRef;
  contest;
  groups;
  date = new Date();

  apply(form){
    let inputEl: HTMLInputElement = this.inputEl.nativeElement;
    let fileCount: number = inputEl.files.length;
    let formData = new FormData();
    if (fileCount > 0) { // a file was selected
      formData.append('file', inputEl.files.item(0));
      formData.append('comid', this.contest.comid);
      formData.append('prizename', form.prizename);
      formData.append('groupsno', form.groupsno);
      formData.append('awardtime', form.awardtime);
      this.http.post("http://localhost:8080/CompSystem/teacher/setCompResult", formData)
        .subscribe(() => {
          this.dialogRef.close();
          alert("反馈成功!");
        });
    }
  }
  constructor(public dialogRef: MdDialogRef<DoneDialog>,private http: Http){}
  ngOnInit(){
    if(this.contest){
      this.http.get("http://localhost:8080/CompSystem/teacher/getCompGroups" +"?comId=" + this.contest.comid)
        .map(response => console.log("DoneDialog:",response.json()) || response.json())
        .subscribe(response => this.groups = response);
    }
  }
}



