import { Component, OnInit } from '@angular/core';
import { Location }               from '@angular/common';
import { NgForm, FormControl } from '@angular/forms';
import { Http, Headers, Response, RequestOptions, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import { ActivatedRoute, Params }   from '@angular/router';
import 'rxjs/add/operator/switchMap';

import { ArticleType } from '../../_models';
import { LoginService, ArticleService, ContestService,AwardService } from '../../_services';
import { ARTICLETYPES } from '../../_datas';

@Component({
  selector: 'app-article-edit',
  templateUrl: './article-edit.component.html',
  styleUrls: ['./article-edit.component.css'],
  providers: [LoginService, ArticleService, ContestService,AwardService]
})
export class ArticleEditComponent implements OnInit {
  article;
  editMode = false;
  admin;
  now = new Date();
  ARTICLETYPES: ArticleType[];
  contests;
  awards;
  contest;
  award;
  editorContent: Object;
  content = "";
  articleType;
  articleTitle;
  relationId;

  setContent(comid, articleType) {
    if (comid) {
      var url;
      if (articleType == 1) {
        url = "http://localhost:8080/CompSystem/teacher/getCompDetail" + "?comId=" + comid;
      } else if (articleType == 3) {
        url = "http://localhost:8080/CompSystem/admin/awardsByComp" + "?comId=" + comid;
      }
      this.http.get(url)
        .map(response => console.log("DeclarationDetailComponent:", response.json()) || response.json())
        .subscribe(response => {
          if (articleType == 1 && confirm("加载初始化竞赛内容到编辑栏吗?")) {
            this.editorContent = `<p>竞赛名:${response.competition.comname}</p>`;
            this.editorContent += `<p>简介:${response.competition.introduce}</p>`;
            this.editorContent += `<p>主办方:${response.competition.host}</p>`;
            this.editorContent += `<p>协办方:${response.competition.department}</p>`;
            this.editorContent += `<p>申请开始时间:${response.competition.applybegindate}</p>`;
            this.editorContent += `<p>申请结束时间:${response.competition.applyenddate}</p>`;
            this.editorContent += `<p>竞赛时间:${response.competition.time}</p>`;
            this.editorContent += `<p>竞赛对象:${response.competition.object}</p>`;
          } else if (articleType == 3 && confirm("加载初始化获奖情况到编辑栏吗?")) {

          }
        });
    }
  }
  constructor(private route: ActivatedRoute, private location: Location,
    private loginService: LoginService, private http: Http, private contestService: ContestService,
    private articleService: ArticleService, private awardService: AwardService) {
    Object.assign(this, { ARTICLETYPES });
  }

  compCtrl: FormControl;
  filteredComps: any;
  ngOnInit() {
    this.route.params
      .switchMap((params: Params) => this.articleService.getArticle(+params['id']))
      .subscribe(article => {
        this.article = article;
        if (this.article) {
          this.editMode = true;
          this.articleTitle = this.article.title;
          this.articleType = this.article.articleType;
          this.editorContent = this.article.content;
          this.relationId = this.article.relationId;
        }
      });
    this.route.params
      .switchMap((params: Params) => this.contestService.getUnPubComp(+params['comid']))
      .subscribe(contest => {
        this.contest = contest;
        if (this.contest) {
          this.http.get("http://localhost:8080/CompSystem/teacher/getTeacherInfo" + "?teacherNo=" + this.contest.leaderNo)
            .map(response => response.json())
            .subscribe(response => {
              this.editorContent += `<p>负责人:${response.teachername}</p>`;
            });
          this.articleType = 1;
          this.relationId = this.contest.comid;
          this.editorContent = `<p>竞赛名称:${this.contest.comname}</p>`;
          this.editorContent += `<p>主办系别:${this.contest.unit}</p>`;
          this.editorContent += `<p>竞赛等级:${this.contest.levelName}</p>`;
          this.editorContent += `<p>竞赛地点:${this.contest.place}</p>`;
          this.editorContent += `<p>竞赛时间:${this.contest.time}</p>`;
          this.editorContent += `<p>报名时间:${this.contest.applyBeginDate} - ${this.contest.applyEndDate}</p>`;
          this.editorContent += `<p>参赛对象:${this.contest.object}</p>`;
          this.editorContent += `<p>报名费用:${this.contest.cost}</p>`;
          this.editorContent += `<p>是否组队:${this.contest.isNeedGroup}</p>`;
          this.editorContent += `<p>组队人数:${this.contest.groupNum}</p>`;
        }
      });
      this.route.params
        .switchMap((params: Params) => this.awardService.getUnPubAward(+params['awardid']))
        .subscribe(award => {
          this.award = award;
          if(this.award){
            this.articleType = 3;
            this.relationId = this.award.competition.comid;
            this.editorContent = `<p>竞赛名称:${this.award.competition.comname}</p>`;
            this.award.awardsList.forEach((e,i,a) => {
              this.editorContent += `<diav>`;
              this.editorContent += `<p>队伍名:${e.groupsName}</p>`;
              this.editorContent += `<p>队员:${e.groupMember}</p>`;
              this.editorContent += `<p>奖项:${e.prizeName}</p>`;
              this.editorContent += `<p>指导教师:${e.teacher}</p>`;
              this.editorContent += `<p>获奖时间:${e.awardTime}</p>`;
              this.editorContent += `<p>奖状:<img alt="奖状" src="${e.awardsImg}"/></p>`;
              this.editorContent += `</diav>`;
            });
          }
        });
    this.admin = this.loginService.getUser();
    this.compCtrl = new FormControl();
    this.filteredComps = this.compCtrl.valueChanges
      .startWith(null)
      .map(name => this.filterComps(name));
    this.getContests();
    this.getAwards();
  }
  filterComps(val) {
    return val ? this.contests.filter(c => new RegExp(`^${val}`, 'gi').test(c.comname))
      : this.contests;
  }
  displayFn(comp) {
    console.log("DeclarationComponent.displayFn(content):", comp);
    if (comp) {
      this.http.get("http://localhost:8080/CompSystem/teacher/getCompDetail" + "?comId=" + comp.id)
        .map(response => console.log("DeclarationDetailComponent:", response.json()) || response.json())
        .subscribe(response => {
          if (confirm("加载竞赛内容到编辑栏吗?")) {

          }
        });
    }
    return comp ? comp.name : comp;
  }

  getContests() {
    this.http.get("http://localhost:8080/CompSystem/admin/unPub" + "?type=1")
      .map(response => console.log("ArticleEditComponent.getContests:", response.json()) || response.json())
      .subscribe(response => this.contests = response);
  }

  getAwards() {
    this.http.get("http://localhost:8080/CompSystem/admin/unPub" + "?type=2")
      .map(response => console.log(`ArticleEditComponent.getAwards:`, response.json()) || response.json())
      .subscribe(response => this.awards = response);
  }

  logChange({ editor, html, text }) {
    console.log(editor, html, text);
    console.log("editorContent", this.editorContent);
  }

  update(article) {
    let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8' });
    let options = new RequestOptions({ headers: headers });
    let urlSearchParams = new URLSearchParams();
    urlSearchParams.append('title', article.title);
    urlSearchParams.append('content', "" + this.editorContent);
    urlSearchParams.append('articleId', this.article.articleId);
    console.log(urlSearchParams.toString());
    this.http.post("http://localhost:8080/CompSystem/admin/updateArticle", urlSearchParams)
      .map(response => console.log("ArticleEditComponent.update", response.json()) || response.json())
      .subscribe(response => {
        if (response == 1) {
          this.goBack();
          alert("更新成功!");
        } else {
          alert("更新失败!");
        }
      });
  }

  save(article) {
    // /admin/relationId
    console.log("editorContent:", this.editorContent);
    this.articleService.pubArticle({
      title: article.title,
      content: this.editorContent,
      articleType: article.articleType,
      relationId: article.relationId
    }).subscribe(response => {
      if (response == 1) {
        this.goBack();
        alert("发布成功!");
      } else {
        alert("发布失败!");
      }
    });
  }

  goBack(): void {
    this.location.back();
  }
}



