import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';

import { ContestService, LoginService } from '../../../_services';
import { Contest } from '../../../_models';

@Component({
  selector: 'app-teacher-contest',
  templateUrl: './teacher-contest.component.html',
  styleUrls: ['./teacher-contest.component.css'],
  providers: [ContestService, LoginService]
})
export class TeacherContestComponent implements OnInit {
  teacher;
  declarations;
  show = 'applied';
  filterContent = [
    { name: "已申报竞赛列表", value: "applied" },
    { name: "待审批竞赛列表", value: "waiting" },
    { name: "审批通过的竞赛列表", value: "pass"},
    { name: "审批不通过的竞赛列表", value: "back"},
    { name: "可以报名的竞赛", value: "applying" },
    { name: "竞赛中的竞赛", value: "contesting" },
    { name: "竞赛已结束的竞赛", value: "end"}
  ];

  pass(group, pass) {
    this.http.get("http://localhost:8080/CompSystem/teacher/manageGroupsMember" + "?groupNo=" + group.groupsno + "&status=" + pass)
      .map(response => console.log("TeacherContestComponent.pass:", response.json()) || response.json())
      .subscribe(response => group.status = 1);
  }

  getDeclarations() {
    var url = "http://localhost:8080/CompSystem/teacher/getResponOrApplyComp" + "?teacherNo=" + this.teacher.teacherno;
    switch (this.show) {
      case 'applying': {
        url = "http://localhost:8080/CompSystem/teacher/getResponOrApplyComp" + "?teacherNo=" + this.teacher.teacherno + "&btnStatus=4";
      }
      break;
      case 'waiting': {
        url = "http://localhost:8080/CompSystem/teacher/getResponOrApplyComp" + "?teacherNo=" + this.teacher.teacherno + "&btnStatus=1";
      }
      break;
      case 'pass': {
        url = "http://localhost:8080/CompSystem/teacher/getResponOrApplyComp" + "?teacherNo=" + this.teacher.teacherno + "&btnStatus=2";
      }
      break;
      case 'back': {
        url = "http://localhost:8080/CompSystem/teacher/getResponOrApplyComp" + "?teacherNo=" + this.teacher.teacherno + "&btnStatus=3";
      }
      break;
      case 'contesting': {
        url = "http://localhost:8080/CompSystem/teacher/getResponOrApplyComp" + "?teacherNo=" + this.teacher.teacherno + "&btnStatus=5";
      }
      break;
      case 'end': {
        url = "http://localhost:8080/CompSystem/teacher/getResponOrApplyComp" + "?teacherNo=" + this.teacher.teacherno + "&btnStatus=6";
      }
      break;
      default: {
        url = "http://localhost:8080/CompSystem/teacher/getResponOrApplyComp" + "?teacherNo=" + this.teacher.teacherno;
      }
    }
    this.http.get(url)
      .map(response => console.log("ContestService.getContests:", response.json()) || response.json())
      // this.contestService.getDeclarationsByTeacher(this.teacher.teacherno)
      .subscribe(declarations => {
        this.declarations = declarations;
        if (this.declarations) {
          this.declarations.forEach((currentValue, index, array) => {
            array[index].groupToggle = false;
            this.http.get("http://localhost:8080/CompSystem/teacher/getCompGroupsAndMember" + "?comId=" + currentValue.comid)
              .map(response => response.json())
              .subscribe(response => array[index].groups = response);
          });
        }
        console.log(this.declarations);
      });
  }

  toggleGroup(declaration) {
    declaration.groupToggle = !declaration.groupToggle;
  }

  delete(declaration) {
    this.http.get("http://localhost:8080/CompSystem/teacher/deleteComp" + "?comId=" + declaration.comid)
      .subscribe(() => {
        this.getDeclarations();
        alert("删除成功!");
      }
      );
  }

  constructor(private contestService: ContestService, private loginService: LoginService,
    private http: Http) { }

  ngOnInit() {
    this.teacher = this.loginService.getUser();
    this.getDeclarations();
  }
}



